<template>
  <el-container>
    <el-header class="text-center" style="height: auto; padding: 0px">
      <layout></layout>
    </el-header>
    <el-main>
      <el-button :plain="true" @click="logout">Logout</el-button>
      ADMIN PAGE
    </el-main>
    <el-footer class="text-center" style="background-color: #a66081;height: auto; padding: 5px">
      <small style="color: white"><b>©2022 Gülşah Alıcı</b></small>
    </el-footer>
  </el-container>
  <div>
  </div>
</template>

<script>
import Layout from './Layout.vue'
import { computed, onBeforeMount } from 'vue'
import { useRouter } from 'vue-router'
import firebase from 'firebase'
import { ElMessage } from 'element-plus'

export default {
  components: { Layout },
  props: {
    admin: Object
  },
  setup(props) {

    const router = useRouter()

    const adminGulsah = computed(() => {
      return props.admin
    })

    const logout = () => {
      firebase.auth().signOut()
        .then(() => {
          ElMessage({
            message: 'Logged out!',
            type: 'success',
          })
          router.push({path: '/admin/login' })
        })
        .catch(err => {
          ElMessage({
            message: err.message,
            type: 'error',
          })
        })
    }

    onBeforeMount(() => {
      if(!adminGulsah.value) {
        router.push({path: '/admin/login' })
      }
    })

    return {
      logout,
      adminGulsah
    }
  }
}
</script>

